import _ from 'lodash';
import api from 'utils/api'
function sortBy(arr) { //
    return _.sortBy(arr, (item) => {
        return item.sortId
    })
}

function getRoles(callback, type, modules) { //
    api.get(`/role`).then(res => {
        let datas = res.data.map(item => {
            const obj = {
                id: item.id,
                dKey: item.id,
                dValue: item.sign
            }
            return obj
        })
        if( type != 'filter' ){
            datas.unshift({
                id: '',
                dKey: '',
                dValue: ''
            })
        }
        if ( modules === 'less' ){
            const arr = ['',''];
            datas = datas.filter(item => {
                return arr.indexOf(item.dValue) == -1
            })
        }
        if (callback) callback(datas)
        return res.data;
    })
    .catch(err => {
        console.log(err)
    });
    return [];
}

